.i{
    display: flex;
    height: 100vh;
}

.i-left{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.i-right{
    flex: 1;
}

.i-left-wrapper{
    padding: 50px;
    height: 50%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.i-intro{
    font-size: 32px;
    font-weight: 300;
}

.i-name{
    font-size: 64px;
}

.i-title{
    height: 50px;
    overflow: hidden;
}

.i-title-wrapper{
 height: 100%;
 animation: move 10s ease-out infinite alternate;
}

@keyframes move{
    25%{transform: translateY(-50px);}
    50%{transform: translateY(-100px);}
    75%{transform: translateY(-150px);}
    100%{transform: translateY(-200px);}
}

.i-title-item{
    height: 50px;
    font-size: 32px;
    font-weight: bold;
    color: brown;
    display: flex;
    align-items: center;
}

.i-img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 20px;
}

.i-desc{
    font-size: 16px;
}

@media screen and (max-width: 480px) {
    .i {
      flex-direction: column;
      height: 100%;
    }
  
    .i-left-wrapper {
    
      padding: 10px;
      align-items: center;
      justify-content: center;
      text-align: center;
      height: 100%;
    }

    .i-title-item{
        align-items: center;
        justify-content: center;
        text-align: center;
    }
  
    .i-desc{
      margin-top: 30px;
    }
    
  }